import React, { useState, useEffect } from "react";
import './Poll.css';

const Poll = ({question, answers, onVote, voted, voteReady}) => {
  const [totalVotes, setTotalVotes] = useState(0)
  useEffect(() => {
    const _totalVotes = answers?.reduce((total, current) => (total+parseInt(current.votes)), 0)
    console.log('totalVotes: ', _totalVotes)
    setTotalVotes(_totalVotes)
  }, [answers])
  const renderAnswers = () => {
    return (
      <ul className='poll-answers'>
        {answers?.map(answer => (
          <li key={answer?.option} style={{width: '100%', margin: '5px 0'}}>
            <button className="option" onClick={() => onVote(answer?.option)}>{answer?.option}</button>
          </li>
        ))}
      </ul>
    )
  }

  const renderStatus = () => {
    return (
      <ul className='poll-answers'>
        {answers?.map(answer => (
          <li key={answer?.option} style={{width: '100%', margin: '5px 0'}}>
            <div className="answer-status">
              {voted==answer?.option && <div className="answer-fill"></div>}
              <div className="answer-labels">
                <span style={{color: 'rgb(48, 48, 48)'}}>{answer?.votes*100/totalVotes}%</span>
                <span className={voted==answer?.option ? "answer vote" : "answer"}>{answer?.option}</span>
              </div>
            </div>
          </li>
        ))}
      </ul>
    )
  }

  return (
    <article style={{padding: '10px'}}>
      <h3 className="header">{question}</h3>
      {voted ? renderStatus() : renderAnswers()}
      <p className="votes">{totalVotes} votes</p>
    </article>
  )
}

export default Poll;